.App {
  display: flex;
  flex-direction: column;
  min-height: 100vh;
}

.totalContainer {
  display: flex;
  flex-direction: column;
  flex-grow: 1;
}

.mainContent{
  flex-grow: 1;
}

body {
  /*background of the login page*/
  margin: 0;
  padding: 0;
  font-family: 'Segoe UI', Tahoma, Geneva, Verdana, sans-serif;
background-image: linear-gradient(
  240deg,
  hsl(0deg 0% 100%) 0%,
  hsl(60deg 1% 88%) 4%,
  hsl(60deg 1% 77%) 9%,
  hsl(60deg 1% 65%) 16%,
  hsl(33deg 3% 59%) 26%,
  hsl(18deg 5% 54%) 49%,
  hsl(0deg 5% 48%) 79%,
  hsl(0deg 4% 39%) 88%,
  hsl(0deg 3% 30%) 93%,
  hsl(0deg 0% 21%) 96%,
  hsl(0deg 1% 21%) 98%,
  hsl(0deg 2% 21%) 99%,
  hsl(0deg 4% 21%) 100%
);
}

@keyframes draw {
  from {
    width: 0;
    height: 0;
  }
  to {
    width: 15px;
    height: 30px;
  }
}

@keyframes pop {
  0% {
    transform: scale(0);
    transform: translateY(-100px);
    opacity: 0;
  }

  10% {
    transform: scale(1.5);
    transform: translateY(-50px);
    opacity: 0.5;
  }

  100% {
    transform: scale(1);
    transform: translateY(0px);
    opacity: 1;
  }
}

@keyframes pulse {
  0% {
      transform: scale(0.8);
      opacity: 0.6;
  }
  70% {
      transform: scale(1.2);
      opacity: 0;
  }
  100% {
      transform: scale(1.4);
      opacity: 0;
  }
}

@keyframes fadeIn {
  from {
    opacity: 0;
    transform: translateY(20px);
  }
  to {
    opacity: 1;
    transform: translateY(0);
  }
}

@keyframes fadeInReverse {
  from {
    opacity: 0;
    transform: translateY(-20px);
  }
  to {
    opacity: 1;
    transform: translateY(0);
  }
}

@keyframes slideIn {
  from {
    transform: translateY(-30px);
    opacity: 0;
  }
  to {
    transform: translateY(0);
    opacity: 1;
  }
}

@keyframes slideInUp {
  from {
    transform: translateY(30px);
    opacity: 0;
  }
  to {

    transform: translateY(0);
    opacity: 1;
  }
}

@keyframes flipIn {
  from {
    transform: perspective(400px) rotateX(90deg);
    opacity: 0;
  }
  to {
    transform: perspective(400px) rotateX(0deg);
    opacity: 1;
  }
}